import * as C from "../components";

export const render = {
  markdownLink: (text: string) => {
    const regLink = /\[[^\[\]\(\)]*\]\([^\[\]\(\)]*\)/g;
    const stringMe = text.split(regLink);
    const target = [];
    let keyIndex = 1;
    text.replace(regLink, (match) => {
      const list = match.split(/(\[|\]\(|\))/g);
      const str = stringMe.shift();
      target.push(
        <span key={"span_" + keyIndex}>{str}</span>,
        <C.A href={list[4]} key={"link_" + keyIndex}>
          {list[2]}
        </C.A>
      );
      keyIndex += 1;
      return "";
    });
    target.push(...stringMe);

    return target;
  },
};
